import {
	uniGrid
} from '@dcloudio/uni-ui'
import {
	uniGridItem
} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			bannerimg: [],
			selectedIndex: 1,
			navimg: [],
			options: {},
			adimg: {},
			products: [],
			scrollTop: 0,
			old: {
				scrollTop: 0
			}
		}
	},
	components: {
		uniGrid,
		uniGridItem
	},
	onLoad() {

	},
	created() {
		this.getbanner();
		this.getproduct();
	},
	methods: {
		async getbanner() {
			const res = await this.myRuquest({
				url: 'addons/xshop/nav',
				method: 'POST',
				data: {
					nav_type: [0, 1, 2]
				}
			});
			// console.log(res);
			this.bannerimg = res.data.data[0];
			this.navimg = res.data.data[1];
			this.adimg = res.data.data[2][0];
		},
		async getproduct() {
			const res = await this.myRuquest({
				url: "addons/xshop/product/getHomeProducts",
			})
			this.products = res.data.data.home_recommend_products;
			console.log(this.products);
		},
		change(e) {
			// console.log(e);
			this.selectedIndex = e.target.current + 1;
		}
	}
}
